





















































export default {
	data() {
		return {
			accordionActive: "grace-accordion-1"
		}
	},
	methods:{
		changeAccordion : function(e){
			var accordionIndex = e.currentTarget.id;
			if (this.accordionActive == accordionIndex){accordionIndex = '';}
			this.accordionActive = accordionIndex;
		}
	}
}
